import React, { Component } from 'react';
import Widget from './Widget.js';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">The great thing in this world is not so much where you stand, as in what direction you are moving.</h1>
        </header>
        <p></p>
        <Widget
          title={'Wife'}
          dataKey={'stat-wife'}
          legend={[2,3,4,5]}
          input={1}
          min={1}
          max={5}
        />
        <Widget
          title={'Coq'}
          dataKey={'stat-coq'}
          legend={[1,2,3,4]}
          input={0.5}
          min={0.5}
          max={1}
        />
        <Widget
          title={'Meditation'}
          dataKey={'stat-meditation'}
          legend={[2,5,10,20,30]}
          input={1}
          min={1}
          max={30}
        />
        <Widget
          title={'German'}
          dataKey={'stat-german'}
          legend={[5,15,30,60]}
          input={5}
          min={1}
          max={60}
        />
        <Widget
          title={'Sport'}
          dataKey={'stat-sport'}
          legend={[2,5,10,30,60]}
          input={3}
          min={3}
          max={60}
        />
        <p></p>
      </div>
    );
  }
}

export default App;



